const clone = function(target) {
  var buf;
  if (typeof target == "function") {
    return target;
  }
  if (Array.isArray(target)) {
    buf = []; // 
    var i = target.length;
    while (i--) {
      buf[i] = clone(target[i]);
    }
    return buf;
  }
  if (target instanceof Object) {
    buf = {}; // 
    for (var k in target) { // 
      buf[k] = clone(target[k]);
    }
    return buf;
  }

  return target;
}

/**
 * 
 */
const stringify = function(data) {
  return objToStringify(data).replace(/,[}]/g, "}").replace(/,\]/g, "]")
}
const objToStringify = function(data) {
  if (typeof data == 'function' || typeof data == "number" || typeof data == "boolean") {
    return `${data}`;
  }
  if (typeof data == 'string') {
    return `'${data}'`;
  }
  if (Array.isArray(data)) {
    let str = '[';
    let end = ']';
    data.forEach(item => {
      str += `${stringify(item)},`;
    });
    return str + end;
  }
  if (data instanceof Object) {
    let str = '{';
    let end = '}';
    Object.keys(data).map(key => {
      str += `${key}:${stringify(data[key])},`;
    });
    return str + end;
  }
  return data + '';
}

/**
 * String
 * @param {String} strObj 
 * @param {Object} global {key:'_Vue'[String],val:_Vue[Object]} key:val:
 * @param {any} error 
 */
const parseObj = function(strObj, global, error) {
  try {
    return eval(!global ? `(${strObj})` : `((${global.key})=>{ return ${strObj} })(${global.val})`);
  } catch (e) {
    if (error) {
      throw e;
    }
    return strObj;
  }
}

/**
 * 
 * @param {Object} str
 */
const encryptionChar = ""
const encryption = {
  /**
   * 
   * @param {Object} str
   */
  set: function(str) {

      /**
       * 
       * @param {Object} char
       * @param {Object} binary
       */
      var getCode = function(char, binary) {
        binary = binary || 16;
        return parseInt(char.charCodeAt(0).toString(binary))
      }
      /**
       * 
       */
      var _encry = (function() {
        let n = 0;
        for (let j = 0; j < encryptionChar.length; j++) {
          var _s = encryptionChar[j];
          n += parseInt(getCode(_s, 7));
        }
        return n;
      })();
      var _str = '';
      for (let i = 0; i < str.length; i++) {
        var _b = parseInt(getCode(str[i], 9));
        _b += _encry;
        _str += _b + (i < str.length - 1 ? "0y" : '');
      }
      return encodeURI(_str);
    }
    /**
     * 
     * @param {Object} str
     */
    ,
  get: function(str) {
    /**
     * 
     * @param {Object} char
     * @param {Object} binary
     */
    var getCode = function(char, binary) {
      binary = binary || 16;
      return parseInt(char.charCodeAt(0).toString(binary))
    }
    /**
     * 
     */
    var _encry = (function() {
      let n = 0;
      for (let j = 0; j < encryptionChar.length; j++) {
        var _s = encryptionChar[j];
        n += parseInt(getCode(_s, 7));
      }
      return n;
    })();
    str = decodeURI(str);
    let ar = str.split("0y");
    var _str = '';
    for (let i = 0; i < ar.length; i++) {
      var n = parseInt(ar[i]);
      n -= _encry;
      _str += String.fromCharCode(parseInt(n + "", 9));
    }
    return _str;
  }
}

/**
 * @param {Object} str
 */
const hashCode = function(str) {
  var hash = 0,
    i, chr, len;
  if (str.length === 0) return hash;
  for (i = 0, len = str.length; i < len; i++) {
    chr = str.charCodeAt(i);
    hash = ((hash << 5) - hash) + chr;
    hash |= 0; // Convert to 32bit integer
  }
  return hash;
}

/**
 * data int float string object array
 * @param {Object} data
 */
const getDataType = function(data) {
  return typeof data == 'boolean' ? 'boolean' :
    typeof data == 'number' && (data + '').indexOf('.') < 0 ? 'int' :
    typeof data == 'number' && (data + '').indexOf('.') > 0 ? 'float' :
    typeof data == 'string' ? 'string' :
    Object.prototype.toString.call(data) == '[object Object]' ? 'object' :
    Array.isArray(data) ? 'array' : 'string';
}

/**
 *  ()
 * @param {Object} min 
 * @param {Object} max 
 * @param {Object} type 
 * 						0		  ()
 * 						1 		  
 * 						2 		  
 * 						3 		  
 * 
 */
const randomNum = function(min, max, type) {
  var random = Math.random() * (max - min) + min;
  switch (type) {
    case 1:
      return Math.ceil(random);
    case 2:
      return Math.floor(random);
    case 3:
      return Math.round(random);
    default:
      return parseInt(random);
  }
}

