const randomNumFormula = function() {
  var yinshi = ['+', '-', '*', '/'];
  var y = randomNum(0, yinshi.length - 1);

  var num1 = randomNum(0, y > 1 ? 10 : 20, 0);
  var num2 = randomNum(4, 10, 0);
  return `${num1}${yinshi[y]}${num2}`
}

